.ant-layout {
    min-height: 100%;
}

.trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    transition: color 0.3s;
}

.trigger:hover {
    color: #1890ff;
}

.logo {
    height: 32px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: large;
    font-weight: bold;
    a{
        color: rgb(235, 232, 232);
    }
}

.site-layout .site-layout-background {
    background: #fff;
}

.ant-table-wrapper{
    overflow: auto;
}
.site-layout-background{
    
    position: relative;
}

.use-dropdown{
    cursor: pointer;
    position: absolute;
    line-height: normal;
    right: 3rem;
    top: 50%;
    transform: translateY(-50%);
}